.App {

}

.search {
  padding: 1em;
}

.search input {
  line-height: 3em;
  font-size: 2em;
  width: 100%;
  margin: 0;
  background: transparent;
  border: none;
  border-bottom: 2px solid rgba(255,255,255,0.6);
  color: #fff;
  outline: none;
}

.grid {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(auto-fill, minmax(250px,1fr));
  grid-auto-rows: 20px;
}

.coins {
  list-style: none;
  -ms-flex-pack: center;
      justify-content: center;
  color: #fff;
  padding: 0;
  margin: 0;
}

.coin {
  width: 300px;
  text-align: center;
  cursor: pointer;
  text-align: center;
  line-height: 1em;
  padding-bottom: 1em;
  font-size: 1em;
  margin: 1em;
  border: 1px solid;
  -webkit-box-shadow: 2px 2px 3px rgba(0,0,0,1);
          box-shadow: 2px 2px 3px rgba(0,0,0,1);
  border-radius: 3px;
  position: relative;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  word-wrap: break-word;
  color: #fff;
  background:-webkit-gradient(linear, left top, left bottom, from(#222), color-stop(70%, #333), to(#444));
  background:-webkit-linear-gradient(top, #222 0%, #333 70%, #444 100%);
  background:-o-linear-gradient(top, #222 0%, #333 70%, #444 100%);
  background:linear-gradient(to bottom, #222 0%, #333 70%, #444 100%);
  -webkit-transform-origin: center;
      -ms-transform-origin: center;
          transform-origin: center;
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
  -webkit-transition: all 0.3s ease-in;
  -o-transition: all 0.3s ease-in;
  transition: all 0.3s ease-in;
}

.coin:hover, .coin:active, .coin:focus {
  -webkit-transform: scale(1.2);
      -ms-transform: scale(1.2);
          transform: scale(1.2);
  -webkit-box-shadow: 1px 1px 1px rgba(0,0,0,0.3);
          box-shadow: 1px 1px 1px rgba(0,0,0,0.3);
  z-index: 1;
}

.coin .title {
  margin-top: 1em;
  font-weight: bold;
}

img {
  max-width: 100%;
  height: 200px;
  -o-object-fit: cover;
     object-fit: cover;
}

.display {
  color: #fff;
}

.ticker {
  font-size: 1.2em;
  text-align: center;
}

.red {
  color: red;
  font-weight: bold;
  -webkit-transition: color 0.3s ease-in;
  -o-transition: color 0.3s ease-in;
  transition: color 0.3s ease-in;
}

.green {
  color: green;
  font-weight: bold;
  -webkit-transition: color 0.3s ease-in;
  -o-transition: color 0.3s ease-in;
  transition: color 0.3s ease-in;
}



